import { Component } from 'js-component-framework';

/*
 * Component for Thesis Tooggle
 */
export default class ThesisToggle extends Component {
  /**
   * Start the component
   */
  constructor(config) {
    // Initiate the component
    super(config);
    const { button } = this.children;
    if (! button) {
      return;
    }
    button.addEventListener('click', () => this.buttonListener(button));
  }

  buttonListener(element) {
    const button = element;
    const { textWrap } = this.children;
    const isExpanded = 'true' === textWrap.getAttribute('aria-expanded');

    if (! isExpanded) {
      button.innerText = '« Read less';
      textWrap.setAttribute('aria-expanded', 'true');
    } else {
      button.innerText = 'Read more »';
      textWrap.setAttribute('aria-expanded', 'false');
    }
  }
}
