import { Component } from 'js-component-framework';
import Headroom from 'headroom.js';
import offset from 'document-offset';
import './stickyHeader.scss';

/*
 * Component for sticky header.
 */
export default class StickyHeader extends Component {
  /**
   * Start the component
   */
  constructor(config) {
    // Initiate the component
    super(config);

    // Do nothing if function has already run.
    if (document.body.classList.contains('header-applied')) {
      return;
    }
    this.hasAnnouncement = document.body
      .classList.contains('enable-announcement-bar');
    if (this.hasAnnouncement) {
      this.hasAnnouncementEl = document.getElementById('announcement-bar');
    }

    // Add flag to so we don't run this function again
    document.body.classList.add('header-applied');

    // Instantiate libraries
    this.initHeadroom();
  }

  /**
   * Applies the Headroom module to this.element
   */
  initHeadroom() {
    // Instantiate headroom
    const { options: { headroom: options } } = this;

    // Use the top of the element to determine when the sticky header should appear.
    options.offset = offset(this.element).top;
    if (this.hasAnnouncementEl) {
      options.offset -= this.hasAnnouncementEl.offsetHeight;
      this.element.style.top = `${this.hasAnnouncementEl.offsetHeight}px`;
    }

    // Instantiate headroom.
    const header = new Headroom(this.element, options);
    header.init();
  }
}

