/**
 * Entry for vertical landing page.
 */

// External dependencies
import { ComponentManager } from 'js-component-framework';

// Components
import portfolioFilters from './portfolioFilters/index';
import customColors from './customColors/index';
import thesisToggle from './thesisToggle/index';
import thesisIntroToggle from './thesisIntroToggle/index';
import stickyHeader from './stickyHeader/index';
import truncateText from './truncateText/index';

// Import SCSS
import '../../scss/verticalLanding/loader.scss';

// Components used by this entry
const components = [
  customColors,
  portfolioFilters,
  stickyHeader,
  thesisToggle,
  thesisIntroToggle,
  truncateText,
];

// Create instance of the component using `rcfp`
const manager = new ComponentManager('a16z');

// Initialize components
window.addEventListener('DOMContentLoaded', () => {
  manager.initComponents(components); // You can supply an array of configurations to start many components at once
});

if (module.hot) {
  module.hot.accept();
  // This is required for HMR, otherwise you'll need to refresh
  // the page to see JS changes
  manager.initComponents(components);
}
