import { Component } from 'js-component-framework';

/*
 * Component for portfolio filters.
 */
export default class PortfolioFilters extends Component {
  static hasRunBefore() {
    // Do nothing if function has already run.
    if (document.body.classList.contains('portfolio-filters-applied')) {
      return true;
    }

    // Add flag to so we don't run this function again
    document.body.classList.add('portfolio-filters-applied');
    return false;
  }

  /**
   * Start the component.
   */
  constructor(config) {
    // Initiate the component.
    super(config);

    // Initiate only once.
    if (PortfolioFilters.hasRunBefore()) {
      return;
    }

    const { buttons } = this.children;
    Array.prototype.forEach.call(buttons, (button) => {
      const { filterTarget } = button.dataset;
      if (! filterTarget) {
        return;
      }
      // Add trigger listener.
      button.addEventListener('click', () => this.updateTarget(filterTarget));
    });
  }

  /**
   * Change the value of the element's filter and aria attributes.
   *
   * @param {string} filterTarget New value for the element's filter should be.
   */
  updateTarget(filterTarget) {
    this.element.dataset.filter = filterTarget;

    // add aria-hidden to the companies that are not in this filter.
    const { companies, buttons } = this.children;
    Array.prototype.forEach.call(companies, (company) => {
      const el = company;
      const { subcategory } = el.dataset;

      // Show all companies.
      el.setAttribute('aria-hidden', false);

      // Do no filter if showing all.
      if ('all' === filterTarget) {
        return;
      }

      // Subcategory has multiple values
      if (subcategory.indexOf(';')) {
        const subcategories = subcategory.split(';');
        let hidden = true;
        Array.prototype.forEach.call(subcategories, (item) => {
          if (filterTarget === item.trim()) {
            hidden = false;
          }
        });
        el.setAttribute('aria-hidden', hidden);
      } else if (filterTarget !== subcategory) {
        // Otherwise hide if the filter does not match.
        el.setAttribute('aria-hidden', true);
      }
    });

    Array.prototype.forEach.call(buttons, (button) => {
      const { filterTarget: buttonFilterTarget } = button.dataset;
      const el = button;
      if (filterTarget === buttonFilterTarget) {
        el.setAttribute('aria-selected', true);
      } else {
        el.setAttribute('aria-selected', false);
      }
    });
  }
}
