import { Component } from 'js-component-framework';
import injectStylesheet from 'inject-stylesheet';

/*
 * Component for updating the text colors in the hero.
 *
 * @todo refactor each function into a single function with a map between
 * selectors and the property they should update.
 */
export default class CustomColors extends Component {
  /**
   * Update elements color style.
   * @param  {string} color     New color.
   * @param  {array} selectors  Array of selectors of elements to be updated.
   */
  static updateColors(color, selectors) {
    const { elementsToUpdateColor } = selectors;
    if (! elementsToUpdateColor) {
      return;
    }

    const styles = elementsToUpdateColor.reduce(
      (acc, selector) => ({
        ...acc,
        [selector]: {
          color,
        },
      }),
      {}
    );

    injectStylesheet.injectWithAllowlist(styles, ['color']);

    // Remove any inline styles from these elements.
    Array.prototype.forEach.call(
      document.querySelectorAll(elementsToUpdateColor.join(',')),
      (el) => {
        const element = el;
        element.style.color = '';
      }
    );
  }

  /**
   * Update elements background-color style.
   * @param  {string} color     New color.
   * @param  {array} selectors  Array of selectors of elements to be updated.
   */
  static updateBackgroundColors(color, selectors) {
    if (selectors.elementsToUpdateBackgroundColor) {
      const elementsToUpdateBG = document.querySelectorAll(
        selectors.elementsToUpdateBackgroundColor.join(',')
      );

      elementsToUpdateBG.forEach((el) => {
        const element = el;
        element.style.backgroundColor = color;
      });
    }
  }

  /**
   * Update elements color style.
   * @param  {string} color     New color.
   * @param  {array} selectors  Array of selectors of elements to be updated.
   */
  static updateBorders(color, selectors) {
    if (selectors.elementsToUpdateBorder) {
      const elementsToUpdateBorder = document.querySelectorAll(
        selectors.elementsToUpdateBorder.join(',')
      );

      elementsToUpdateBorder.forEach((el) => {
        const element = el;
        element.style.borderColor = color;
      });
    }
  }

  /**
   * Update elements color style.
   * @param  {string} color     New color.
   * @param  {array} selectors  Array of selectors of elements to be updated.
   */
  static updateFill(color, selectors) {
    if (selectors.elementsToUpdateFill) {
      const elementsToUpdateFill = document.querySelectorAll(
        selectors.elementsToUpdateFill.join(',')
      );

      elementsToUpdateFill.forEach((el) => {
        const element = el;
        element.style.fill = color;
      });
    }
  }

  /**
   * Start the component
   */
  constructor(config) {
    // Initiate the component
    super(config);

    const { colors } = this.options;
    const { dataset } = this.element;

    colors
      // Add color values to the config array.
      .map(({ name, selectors }) => {
        // Look up color value from component's dataset by name.
        const color = dataset[`color${name}`];

        // Early exit if no value to update found.
        if (! color) {
          return false;
        }

        // Return new array with color to update.
        return {
          color,
          selectors,
        };
      })
      // Remove colors that are not set.
      .filter((colorConfig) => colorConfig)
      // Perform color updating actions.
      .forEach(({ color, selectors }) => {
        CustomColors.updateColors(color, selectors);
        CustomColors.updateBackgroundColors(color, selectors);
        CustomColors.updateBorders(color, selectors);
        CustomColors.updateFill(color, selectors);
      });
  }
}
