import { Component } from 'js-component-framework';

/*
 * Component for Trucate Text component.
 */
export default class TruncateText extends Component {
  /**
   * Start the component
   */
  constructor(config) {
    // Initiate the component
    super(config);
    const { postTitles } = this.children;
    Array.prototype.forEach.call(postTitles, (title) => {
      const text = title;
      // Limit the post titles to 80 characters and add '...' to the end
      text.innerText =
        text.innerText.substring(0, 80) +
        (80 < text.innerText.length ? '...' : '');
    });
  }
}
